import React from "react";
import {
  MDBContainer,
  MDBCol,
  MDBCard,
  MDBCardBody,
  MDBIcon,
  MDBInput,
  MDBSelect,
  MDBSelectOptions,
  MDBSelectInput,
  MDBSelectOption,
  MDBBtn
} from "mdbreact";
const EmailNotifications = () => {
  return (
    <MDBContainer>
      <MDBCol lg="8" md="12" className='d-flex justify-content-center mb-4'>
        <MDBCard>
          <MDBCardBody>
            <div className='form-header blue accent-1'>
              <h3>
                <MDBIcon icon='envelope' /> Send An Email:
                  </h3>
            </div>

            <MDBInput
              icon='user'
              iconClass='grey-text'
              label='Subject'
            />
            <MDBSelect label="TO" >
              <MDBSelectInput />
              <MDBSelectOptions>
                <MDBSelectOption disabled>Select Receipient</MDBSelectOption>
                <MDBSelectOption value="1">All Users</MDBSelectOption>
                <MDBSelectOption value="2">Banned Users</MDBSelectOption>
                <MDBSelectOption value="3">Select Users</MDBSelectOption>
              </MDBSelectOptions>
            </MDBSelect>
            <MDBSelect multiple label="If Select Users" >
              <MDBSelectInput />
              <MDBSelectOptions>
                <MDBSelectOption disabled>Select  Users</MDBSelectOption>
                <MDBSelectOption value="1">email1@email.com</MDBSelectOption>
                <MDBSelectOption value="2">email2@email.com</MDBSelectOption>
                <MDBSelectOption value="3">email3@email.com</MDBSelectOption>
              </MDBSelectOptions>
            </MDBSelect>
            <MDBInput
              type='textarea'
              icon='pencil-alt'
              iconClass='grey-text'
              label='Message'
            />
            <div className='text-center'>
              <MDBBtn color='light-blue'>SEND</MDBBtn>
            </div>
          </MDBCardBody>
        </MDBCard>
      </MDBCol>
    </MDBContainer>
  );
};

export default EmailNotifications;
